.page {
  &-content{
    &__search{
      -webkit-appearance: none;
      border: none;
      outline: none;
      background: #EBEBEB;
      border-radius: 10px;

      width: 100%;
      height: 60px;

      padding: 0 32px;

      font-size: 18px;
      &::placeholder{
        color:rgba(#020203,.5);
      }
    }
  }
  &-blog {
    &__grid {
      &-row{
        display: grid;
        grid-template-columns: 770px auto;
        gap: 30px;
        & + &{
          margin-top: 30px;
        }
      }
    }
  }
}

.blog{
  &-grid{
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));

      gap: 30px;
    }
  }
  &-item{
    height: 100%;
    min-height: 363px;
    &__wrapper{
      background: #FFFFFF;
      border-radius: 15px;
      overflow: hidden;

      box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.08);

      height: 100%;

      display: flex;
      flex-direction: column;
    }
    &__header{
      padding: 20px;
      height: 160px;
      flex-shrink: 0;
      &_peach{
        background: #FFEEE6;
      }
      &_green{
        background: #E6FFCC;
      }
      &_purple{
        background: #FFE1FC;
      }
      &_blue{
        background: #DBF6FF;
      }
    }
    &__title{
      font-family: 'Inter', sans-serif;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      letter-spacing: -0.2px;

      color: #000000;
    }
    &__content{
      padding: 20px 30px 20px 20px;

      position: relative;

      height: 100%;

      display: flex;
      flex-direction: column;
      &::before{
        content: '';
        position: absolute;
        left: 17px;
        top: calc(50% - 150px);

        width: 3px;
        height: 125px;
        display: block;

        background: #FF7940;
      }
    }
    &__desc{
      padding: 0 0 0 20px;
    }
    &__footer{
      margin: auto 0 0 0;
      padding:15px 5px 5px 5px;
    }
    &__text{
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 136%;

      color: #020203;
    }
  }
  &-article{
    &__wrapper{
      max-width: 670px;
      width: 100%;
    }
    &__title{
      font-family: Arial, sans-serif;
      font-weight: 700;
      font-size: 30px;
      line-height: 44px;
      color: #020203;
    }
    &__tag{
      margin-top: 20px;
    }
    &__img{
      margin-top: 35px;

      width: 100%;
      height: 230px;
      border-radius: 10px 10px 0px 0px;
      overflow: hidden;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
        object-position: center center;
      }
    }
    &__content{
      margin-top: 30px;
    }
    &__description{
      font-family: 'Inter', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;

      color: #000000;
      p + p{
        margin-top: 30px;
      }
      p + blockquote{
        margin-top: 35px;
      }
      blockquote + p{
        margin-top: 35px;
      }
      h4 + p{
        margin-top: 30px;
      }
      p + h4{
        margin-top: 30px;
      }
      h4 + blockquote{
        margin-top: 35px;
      }
      blockquote + h4{
        margin-top: 35px;
      }

      blockquote{
        padding-left: 20px;
        position: relative;

        display: flex;
        align-items: center;
        &::before{
          content: '';
          position: absolute;
          left: 0;
          top: -5px;

          height: calc(100% + 10px);
          width: 3px;
          display: block;
          background: #FF7940;
        }
      }
      h4{
        font-family: 'Inter', sans-serif;
        font-weight: 600;
        font-size: 21px;
        line-height: 25px;
        letter-spacing: -0.2px;

        color: #020203;
      }
    }
  }
}


@media screen and (max-width: 1100px) {
  .page{
    &-blog{
      &__grid{
        &-row{
          grid-template-columns: minmax(0,1fr);
        }
        &-item{
          order: 2;
          &_filter{
            order: 1;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .blog{
    &-grid{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
      }
    }
    &-article{
      &__img{
        height: auto;
      }
      &__content{
        margin-top: 15px;
      }
      &__description{
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
}